/* font families */
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
/* logo here  */
.logo {
    width: 220px;
}

.nav-body
{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}
.navbar
{
    padding-top: 20px;
}
/* Navbar Starts Here */
.Navigator{
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;

}
.Navigator ul {
    display: flex;
    justify-content: space-around;
    gap: 20px;
}
.Navigator ul li 
{
    list-style: none;
}
.nav_elements {
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav_elements nav ul li a{
    text-decoration: none;
    color:#232524;

}

ul li a{
    text-decoration: none;
}
.Navigator ul li a
{
    text-decoration: none;
    color: #232524;
}


.logo{
    padding: 20px;
}

 
    .search
    {
        padding: 0px 20px 0px 20px;
}
/* Navbar Ends Here */

